function isAppliedTo(setting, key) {
    return (setting.appliedTo && setting.appliedTo.elements && setting.appliedTo.elements.includes(key))
        || (setting.appliedTo && setting.appliedTo.fields && setting.appliedTo.fields.includes(key));
}
function getElementsThatDependsOnSetting(elements, settingKey) {
    return elements.filter((element) => {
        const elementWithCondition = element.conditions.filter((condition) => condition.condition === 'settings')
            .filter((condition) => condition.settings.map((conditionSetting) => {
            return conditionSetting.key;
        }).includes(settingKey));
        return elementWithCondition.length > 0;
    });
}
function getSettingsThatDependOnSetting(settings, settingKey) {
    return settings.filter((setting) => {
        const elementWithCondition = setting.conditions.filter((condition) => condition.condition === 'settings')
            .filter((condition) => condition.settings.map((conditionSetting) => {
            return conditionSetting.key;
        }).includes(settingKey));
        return elementWithCondition.length > 0;
    });
}
function getElements(elements, settings, required) {
    return elements.filter((item) => {
        return item.required === required;
    }).map((item) => {
        const elementSettings = settings.filter((setting) => {
            return isAppliedTo(setting, item.key);
        }).map((setting) => {
            const options = setting.value.type == 'options' && setting.value.options || [];
            const elementsDependedOnSetting = getElementsThatDependsOnSetting(elements, setting.key);
            const settingsDependedOnSetting = getSettingsThatDependOnSetting(settings, setting.key);
            const hasElementDependendOnSetting = elementsDependedOnSetting.length > 0 || settingsDependedOnSetting.length > 0;
            return Object.assign(Object.assign({}, setting), { options: hasElementDependendOnSetting && options || [] });
        });
        return Object.assign(Object.assign({}, item), { settings: elementSettings });
    });
}
function getFields(fields, settings) {
    const fieldsList = fields.map((field) => {
        const fieldSettings = settings.filter((setting) => {
            return isAppliedTo(setting, field.key);
        });
        return Object.assign(Object.assign({}, field), { settings: fieldSettings || [] });
    });
    return fieldsList;
}
function getRequiredInstance(elements) {
    return elements.filter((element) => element.requiresInstance).length > 0;
}
export default function uiService(title, schema) {
    if (title === 'Auto Video' && schema.elements.length === 0 && schema.settings.length === 0) {
        return null;
    }
    const ui = {
        requiredElements: getElements(schema.elements, schema.settings, true),
        fields: schema.fields && getFields(schema.fields, schema.settings) || [],
        notRequiredElements: getElements(schema.elements, schema.settings, false),
        requiredInstance: getRequiredInstance(schema.elements),
    };
    return ui;
}
