import { getSchemaItem, createSchemaSelectorFromSchema, } from '@src/services/Attributes/Schema/SchemaService';
import { queryChildrenOfElements } from '@src/services/DOM/Queries/QueriesService';
const highlightStyle = {
    ['backgroundColor']: {
        keyHyphenCase: 'background-color',
        value: 'rgba(0, 138, 0, 0.5)',
    },
    ['box-shadow']: {
        keyHyphenCase: 'box-shadow',
        value: '2px 1px 1px black',
    },
    ['opacity']: {
        keyHyphenCase: 'opacity',
        value: '0.5',
    },
};
function createElementHighlight(key, schema, schemaSettings) {
    const schemaItem = getSchemaItem(schema, 'elements', key);
    const { appliedTo } = schemaItem;
    const htmlElements = appliedTo.map((domSelector) => {
        return domSelector.selectors.map((selector) => {
            const selectorElements = document.querySelectorAll(selector);
            return Array.from(selectorElements);
        }).flat();
    }).flat();
    return {
        elements: htmlElements,
        backupStyles: [],
    };
}
function createElementSettingHighlight(key, schema, schemaSettings) {
    const schemaItem = getSchemaItem(schema, 'settings', key);
    const { appliedTo, } = schemaItem;
    const htmlElements = appliedTo.elements && appliedTo.elements.map((element) => {
        const elementSelector = createSchemaSelectorFromSchema(schema, 'elements', element, schemaSettings);
        const elements = document.querySelectorAll(elementSelector.getElementSelector());
        return Array.from(elements);
    }).flat() || [];
    return {
        elements: htmlElements,
        backupStyles: [],
    };
}
function createFieldHighlight(key, specializationKey, schema, schemaSettings) {
    const schemaItem = getSchemaItem(schema, 'fields', key);
    const { specializations } = schemaItem;
    const selectedSpecialization = specializations.find((specializationEntry) => specializationEntry.key === specializationKey);
    if (!selectedSpecialization) {
        throw new Error(`Selected specialization not exists: ${specializationKey}`);
    }
    const appliedToFields = selectedSpecialization.appliedTo.filter((appliedToEntry) => appliedToEntry.type !== 'component');
    return {
        elements: [],
        backupStyles: [],
    };
}
function createFieldSettingHighlight(key, specialization, schema, schemaSettings) {
    const schemaItem = getSchemaItem(schema, 'settings', key);
    return {
        elements: [],
        backupStyles: [],
    };
}
export function createHighlight(name, type, specializationKey, schema, schemaSettings) {
    switch (type) {
        case 'element':
            return createElementHighlight(name, schema, schemaSettings);
        case 'elementSetting':
            return createElementSettingHighlight(name, schema, schemaSettings);
        case 'field':
            return createFieldHighlight(name, specializationKey, schema, schemaSettings);
        case 'fieldSetting':
            return createFieldSettingHighlight(name, specializationKey, schema, schemaSettings);
    }
}
export function enableHighlight(highlight) {
    const { elements, backupStyles } = highlight;
    let styles = [...backupStyles];
    styles = applyHighlightElements(elements, backupStyles);
    return styles;
}
export function disableHighlight(highlight) {
    const { elements, backupStyles } = highlight;
    let styles = [...backupStyles];
    styles = applyBackupElements(elements, backupStyles);
    return styles;
}
function saveStyle(element, styles) {
    const computedStyles = window.getComputedStyle(element);
    const highlightBackup = {};
    Object.keys(highlightStyle).forEach((key) => {
        const { keyHyphenCase } = highlightStyle[key];
        const property = computedStyles.getPropertyValue(keyHyphenCase);
        highlightBackup[key] = {
            keyHyphenCase: highlightStyle[key].keyHyphenCase,
            value: property,
        };
    });
    styles.push(highlightBackup);
    return styles;
}
export function applyHighlightElements(elements, styles) {
    elements.forEach(function (element) {
        styles = applyHighlightElement(element, styles);
    });
    return styles;
}
export function applyHighlightElement(element, styles) {
    styles = saveStyle(element, styles);
    highlightElement(element);
    return styles;
}
export function rollbackElement(element, styles) {
    const backupStyles = styles.shift();
    if (backupStyles === undefined) {
        throw new Error('No backup style found');
    }
    rollbackStyleElement(element, backupStyles);
    return styles;
}
export function applyBackupElements(elements, styles) {
    elements.forEach(function (element) {
        styles = rollbackElement(element, styles);
    });
    return styles;
}
export function applyBackupChildrenOfSelectors(selectors, styles) {
    if (selectors && selectors.length > 0) {
        const elements = queryChildrenOfElements(selectors);
        if (Array.isArray(elements)) {
            elements.forEach((element) => {
                const backupStyles = styles.shift();
                if (backupStyles === undefined) {
                    throw new Error('No backup style found');
                }
                rollbackStyleElement(element, backupStyles);
            });
        }
    }
    return styles;
}
export function highlightElement(element) {
    Object.keys(highlightStyle).forEach((key) => {
        const { keyHyphenCase, value } = highlightStyle[key];
        element.style.setProperty(keyHyphenCase, value);
    });
}
export function rollbackStyleElement(element, backupStyles) {
    Object.keys(backupStyles).forEach((keyCamelCase) => {
        const { keyHyphenCase, value } = backupStyles[keyCamelCase];
        element.style.setProperty(keyHyphenCase, value);
    });
}
