import { getSchemaItem, createSchemaSelectorFromItem, createSchemaSelectorFromSchema, } from '@src/services/Attributes/Schema/SchemaService';
import { elementsSameNode } from '@src/services/Attributes/AppliedTo/AppliedToService';
import MissingFieldSettingError from './Errors/MissingFieldSettingError';
import conditionsService from '@src/services/Attributes/Conditions/ConditionsService';
import { valueServiceV2 } from '@src/services/Attributes/Values/ValuesService';
import AbstractSchemaError from '@src/services/Errors/AbstractSchemaError';
export function validateFieldSetting(inputSetting, field, schema, schemaSettings) {
    const settingSchema = getSchemaItem(schema, 'settings', inputSetting.setting);
    const { conditions, value, specializations } = settingSchema;
    const instances = specializations && specializations.map((specialization) => specialization.value)
        || [inputSetting.option];
    const validations = instances.map((option) => {
        const settingSelector = createSchemaSelectorFromItem(settingSchema, 'settings', inputSetting.setting, schemaSettings, option);
        const fieldSelector = createSchemaSelectorFromSchema(schema, 'fields', inputSetting.field, schemaSettings, field.input.identifier);
        try {
            const fieldSettingSelector = `${fieldSelector.getElementSelector()}${settingSelector.getAttributeSelector()}`;
            const elementsNode = document.querySelectorAll(fieldSettingSelector);
            const elements = Array.from(elementsNode);
            if (elements.length <= 0) {
                throw new MissingFieldSettingError(settingSelector, fieldSelector);
            }
            if (field.domElement) {
                elementsSameNode(fieldSelector, elements, field.domElement);
            }
            if (conditions && conditions.length > 0) {
                conditionsService(settingSelector, conditions, schema, schemaSettings);
            }
            valueServiceV2(elements, settingSelector.getAttribute(), value, option, settingSelector);
            return null;
        }
        catch (error) {
            if (error instanceof AbstractSchemaError) {
                return {
                    type: error.type,
                    message: error.message,
                };
            }
            else {
                throw error;
            }
        }
    });
    const fieldErrors = validations.filter((fieldApplied) => fieldApplied && fieldApplied.message);
    if (fieldErrors.length > 0) {
        return Object.assign(Object.assign({}, inputSetting), { validation: {
                status: false,
                messages: fieldErrors
            } });
    }
    return Object.assign(Object.assign({}, inputSetting), { validation: {
            status: true,
            messages: [
                {
                    type: 'success',
                    message: `Yup! Setting correctly setup.`,
                }
            ]
        } });
}
