export function persistStore(key, values) {
    localStorage.setItem(key, JSON.stringify(values));
}
export function loadStore(key, defaultValue) {
    const storedWizardIntances = localStorage.getItem(key);
    if (storedWizardIntances === null) {
        return defaultValue;
    }
    const initialState = storedWizardIntances ? JSON.parse(storedWizardIntances) : defaultValue;
    return initialState;
}
