import AbstractSchemaError from '@src/services/Errors/AbstractSchemaError';
export default class FieldLinkNestedCollectionLinkNotFoundError extends AbstractSchemaError {
    constructor(attribute) {
        super();
        this.type = 'field-link-nested-collection-link-not-found';
        const attributeId = this.toAttribute(attribute.getPrettierSelector());
        this.message = [
            this.toHighlight(`The attribute ${attributeId} was found on the Collections CMS Template, but missing the link to each item.`),
            `Add a link to the Collections CMS Template page.`
        ].join(' ');
        Object.setPrototypeOf(this, FieldLinkNestedCollectionLinkNotFoundError.prototype);
    }
}
