import { getSchemaItem, createSchemaSelectorFromItem, createSchemaSelectorFromSchema, } from '@src/services/Attributes/Schema/SchemaService';
import { elementSettingExists } from '@src/services/Attributes/Exists/ExistsService';
import { elementSettingAppliedTo, elementsSameNode } from '@src/services/Attributes/AppliedTo/AppliedToService';
import conditionsService from '@src/services/Attributes/Conditions/ConditionsService';
import valuesService from '@src/services/Attributes/Values/ValuesService';
import AbstractSchemaError from '@src/services/Errors/AbstractSchemaError';
function appliedToSelectors(appliedTo, input, schema, schemaSettings) {
    if (appliedTo === undefined) {
        return [];
    }
    if (appliedTo.length <= 0) {
        return [];
    }
    return appliedTo
        .filter((appliedToElement) => {
        return appliedToElement === input.element;
    })
        .map((appliedToElement) => {
        const elementItem = getSchemaItem(schema, 'elements', appliedToElement);
        const elementSelector = createSchemaSelectorFromSchema(schema, 'elements', appliedToElement, schemaSettings, null);
        return {
            elementAttribute: elementItem,
            elementSelector: elementSelector,
        };
    });
}
export function validateElementSetting(inputSetting, element, schema, schemaSettings) {
    const settingSchema = getSchemaItem(schema, 'settings', inputSetting.setting);
    const { appliedTo, conditions, value } = settingSchema;
    const settingSelector = createSchemaSelectorFromItem(settingSchema, 'settings', inputSetting.setting, schemaSettings, inputSetting.option);
    try {
        const elementsSelectors = appliedToSelectors(appliedTo.elements, inputSetting, schema, schemaSettings);
        elementSettingExists(settingSelector, elementsSelectors);
        if (appliedTo && appliedTo.elements) {
            const appliedToElement = elementSettingAppliedTo(settingSelector, elementsSelectors);
            if (appliedToElement && element.domElement) {
                elementsSameNode(settingSelector, [appliedToElement], element.domElement);
            }
        }
        if (conditions && conditions.length > 0) {
            conditionsService(settingSelector, conditions, schema, schemaSettings);
        }
        valuesService(settingSelector, value, inputSetting.option, elementsSelectors.map((value) => value.elementSelector));
    }
    catch (error) {
        if (error instanceof AbstractSchemaError) {
            return Object.assign(Object.assign({}, inputSetting), { validation: {
                    status: false,
                    messages: [
                        {
                            type: error.type,
                            message: error.message,
                        }
                    ]
                } });
        }
        else {
            throw error;
        }
    }
    return Object.assign(Object.assign({}, inputSetting), { validation: {
            status: true,
            messages: [
                {
                    message: `Yup! Setting ${settingSelector.getPrettierSelector()} correctly setup.`,
                    type: 'success',
                }
            ]
        } });
}
