class Selector {
    constructor(attribute, value, initial = false) {
        this.attribute = attribute;
        this.value = value;
        this.initial = initial;
        this.elements = [];
    }
    getElementKey() {
        if (!this.attribute.endsWith('element')) {
            throw new Error('Unexpected error: trying to get element label from not element selector');
        }
        return this.value.split('-').map((innerValue) => innerValue.replace(/[a-z]/g, match => match.toUpperCase())).join(' ');
    }
    setAttribute(attribute) {
        this.attribute = attribute;
    }
    setValue(value) {
        this.value = value;
    }
    setElement(element) {
        this.elements = [element];
    }
    setElements(elements) {
        this.elements = elements;
    }
    getAttribute() {
        return this.attribute;
    }
    getValue() {
        return this.value;
    }
    getItemLabel() {
        const splitAttribute = this.attribute.split('-');
        const keyAttribute = splitAttribute[splitAttribute.length - 1];
        return keyAttribute === 'element' && 'Element' || 'Setting';
    }
    getElementSelector() {
        if (this.value === '') {
            return this.getAttributeSelector();
        }
        if (this.initial) {
            return `[${this.attribute}="${this.value}"],[${this.attribute}="${this.value}-1"]`;
        }
        return `[${this.attribute}="${this.value}"]`;
    }
    getSelectors(selectors) {
        if (!this.value) {
            throw new Error('Missing required selector value to multiple selectors');
        }
        const elementSelectors = this.getElementSelector().split(',');
        return elementSelectors.map((elementSelector) => {
            return selectors.map((selector) => `${elementSelector} ${selector}`);
        }).flat().join(',');
    }
    getAttributeSelector() {
        return `[${this.attribute}]`;
    }
    getPrettierSelector() {
        return `${this.attribute}="${this.value}"`;
    }
    getInitial() {
        return this.initial;
    }
}
export default Selector;
