import { createSchemaSelectorFromSchema, getSchemaItem, } from '@src/services/Attributes/Schema/SchemaService';
import { assertElementIsParentOfElement, assertElementIsParentOfElements } from '@src/services/DOM/Assertions/AssertionsService';
import AttributeIsNotParentOfError from './Errors/AttributeIsNotParentOfError';
export function isParentOf(elementSelector, conditions, schema, schemaSettings) {
    const selectors = conditions.map((condition) => {
        const typeCondition = condition;
        if (typeCondition.type === 'element') {
            const conditionSchemaSelector = createSchemaSelectorFromSchema(schema, 'elements', typeCondition.element, schemaSettings);
            return conditionSchemaSelector.getElementSelector();
        }
        if (typeCondition.type === 'selector') {
            return typeCondition.selector.map((domSelector) => domSelector.selectors.join(','));
        }
        throw new Error(`Error in bounds of condition`);
    });
    const flatSelectors = selectors.flat();
    let isValid = true;
    try {
        assertElementIsParentOfElements(elementSelector.elements, flatSelectors);
    }
    catch (e) {
        isValid = false;
    }
    if (!isValid) {
        const isParentOfConditions = conditions.filter((condition) => {
            const typeCondition = condition;
            if (typeCondition.type === 'element') {
                const conditionSchemaSelector = createSchemaSelectorFromSchema(schema, 'elements', typeCondition.element, schemaSettings);
                try {
                    assertElementIsParentOfElement(elementSelector.getElementSelector(), conditionSchemaSelector.getElementSelector());
                    return false;
                }
                catch (_a) {
                    return true;
                }
            }
            if (typeCondition.type === 'selector') {
                const conditionSchemaSelector = typeCondition
                    .selector
                    .map((domSelector) => domSelector.selectors.join(','))
                    .join(',');
                try {
                    assertElementIsParentOfElement(elementSelector.getElementSelector(), conditionSchemaSelector);
                    return false;
                }
                catch (_b) {
                    return true;
                }
            }
            return false;
        });
        const isParentOfSelectors = isParentOfConditions.map((condition) => {
            const typeCondition = condition;
            if (typeCondition.type === 'element') {
                const elementSchema = getSchemaItem(schema, 'elements', typeCondition.element);
                return elementSchema.appliedTo;
            }
            if (typeCondition.type === 'selector') {
                return typeCondition.selector;
            }
            throw new Error('Condition out of bounds');
        });
        if (isParentOfSelectors.length <= 0) {
            throw new Error('Unexpected error: not parent of condition is empty.');
        }
        throw new AttributeIsNotParentOfError(elementSelector, isParentOfSelectors);
    }
    return true;
}
